/*Анимация реакт иконки*/
.reactIcon {
  float: center;
  text-align: center;
  font-size: 42px;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 18000ms;
          animation-duration: 18000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear; 
  background: transparent;
}
@-webkit-keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
@keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
.ScrollButton {
  position: absolute;
    float: center;
  text-align: center;
  top: 87%;
  left: 48.45%;
  z-index: 2;
  -webkit-animation: bounce 2s infinite 2s;
  animation: bounce 2s infinite 2s;
  -webkit-transition: all .2s ease-in;
  transition: all .2s ease-in;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.ScrollButton img:nth-child(2){
  display: none;
}
.ScrollButton:hover img:nth-child(2) {
  display: inline;  
}
.ScrollButton:hover img:nth-child(1) {
  display: none;  
}
/*Неиспользуемая анимация иконки скролла*/
@-webkit-keyframes bounce {
  0%,
  100%,
  20%,
  50%,
  80% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}
@keyframes bounce {
  0%,
  100%,
  20%,
  50%,
  80% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}



